@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@500&display=swap");

:root {
  --BodyBg: #fff;
  --MainColor: #11101d;
  --LinkColor: #c2c0be;
  --LinkHover: #f9f5eb;
  --ButtonColor: #fff;
  --TextColor: #fff;
  --CardColor: #1d1b31;
  --ButtonHoverColor: #fff;
  --BgSecoundColor: #191919;
  --ListHover: #1d1b31;
  --black: #1d1b31;
  --white: #fff;
  --red: tomato;
  --standard: 5px;
  --card-height: 261px;
  --card-width: 176px;
  --border-radius: 0.75rem;
  --primary-color: #7257fa;
  --secondary-color: #3c3852;
}

body{
  font-family: "Montserrat", sans-serif;
}
a {
  text-decoration: none;
}
/*  Navigation */
.sidebar {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 260px;
  background: var(--MainColor);
  z-index: 1000;
  user-select: none;
  transition: all 0.5s ease;
  font-family: "Montserrat", sans-serif;
  max-height: auto;
}
.sidebar.close {
  width: 78px;
}
.sidebar .logo-details {
  height: 60px;
  width: 100%;
  display: flex;
  align-items: center;
}
.sidebar .logo-details i {
  font-size: 30px;
  color: var(--TextColor);
  height: 50px;
  min-width: 78px;
  text-align: center;
  line-height: 50px;
}
.sidebar .logo-details .logo_name {
  font-size: 22px;
  color: var(--TextColor);
  font-weight: 600;
  transition: 0.3s ease;
  transition-delay: 0.1s;
}
.sidebar.close .logo-details .logo_name {
  transition-delay: 0s;
  opacity: 0;
  pointer-events: none;
}
.sidebar .nav-links {
  height: 100%;
  padding: 30px 0 150px 0;
  overflow: auto;
}
.sidebar.close .nav-links {
  overflow: visible;
}
.sidebar .nav-links::-webkit-scrollbar {
  display: none;
}
.sidebar .nav-links li {
  position: relative;
  list-style: none;
  transition: all 0.4s ease;
}
.sidebar .nav-links li:hover {
  background: var(--ListHover);
}
.sidebar .nav-links li .iocn-link {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.sidebar.close .nav-links li .iocn-link {
  display: block;
}

.sidebar.close .nav-links li .iocn-link.active {
  background-color: var(--ListHover);
}

.sidebar .nav-links li i {
  height: 50px;
  min-width: 78px;
  text-align: center;
  line-height: 50px;
  color: var(--TextColor);
  font-size: 20px;
  cursor: pointer;
  user-select: none;
  transition: all 0.3s ease;
}
.sidebar .nav-links li.showMenu i.arrow {
  transform: rotate(-180deg);
}
.sidebar.close .nav-links i.arrow {
  display: none;
}

.sidebar .nav-links li a {
  display: flex;
  align-items: center;
  text-decoration: none;
}
.sidebar .nav-links li a .link_name {
  font-size: 18px;
  font-weight: 400;
  color: var(--TextColor);
  transition: all 0.4s ease;
}
.sidebar.close .nav-links li a .link_name {
  opacity: 0;
  pointer-events: none;
}
.sidebar .nav-links li .sub-menu {
  padding: 6px 6px 14px 80px;
  margin-top: -10px;
  background: var(--ListHover);
  display: none;
}
.sidebar .nav-links li.showMenu .sub-menu {
  display: block;
}
.sidebar .nav-links li .sub-menu a {
  color: var(--TextColor);
  font-size: 15px;
  padding: 5px 0;
  white-space: nowrap;
  opacity: 0.6;
  transition: all 0.3s ease;
}
.sidebar .nav-links li .sub-menu a:hover {
  opacity: 1;
}
.sidebar.close .nav-links li .sub-menu {
  position: absolute;
  left: 100%;
  top: -10px;
  margin-top: 0;
  padding: 10px 20px;
  border-radius: 0 6px 6px 0;
  opacity: 0;
  display: block;
  pointer-events: none;
  transition: 0s;
}
.sidebar.close .nav-links li:hover .sub-menu {
  top: 0;
  opacity: 1;
  pointer-events: auto;
  transition: all 0.4s ease;
}
.sidebar .nav-links li .sub-menu .link_name {
  display: none;
}

.sidebar.close .nav-links li .sub-menu .link_name {
  font-size: 18px;
  opacity: 1;
  display: block;
}
.sidebar .nav-links li .sub-menu.blank {
  opacity: 1;
  pointer-events: auto;
  padding: 3px 20px 6px 16px;
  opacity: 0;
  pointer-events: none;
}
.sidebar .nav-links li:hover .sub-menu.blank {
  top: 50%;
  transform: translateY(-50%);
}
.sidebar .profile-details {
  position: fixed;
  bottom: 0;
  width: 260px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: var(--ListHover);
  padding: 12px 0px;
  transition: all 0.5s ease;
}
#ClickToLogin {
  cursor: pointer;
}
.sidebar.close .profile-details {
  background: none;
  padding: 12px 11px;
}
.sidebar.close .profile-details {
  width: 78px;
}
.sidebar .profile-details .profile-content {
  display: flex;
  align-items: center;
}
.sidebar .profile-details img {
  height: 32px;
  width: 32px;
  object-fit: cover;
  border-radius: 16px;
  margin: 0 14px 0 12px;
  background: var(--ListHover);
  transition: all 0.5s ease;
}
.sidebar.close .profile-details img {
  padding: 2px;
}
.sidebar .profile-details .profile_name,
.sidebar .profile-details .job {
  color: var(--TextColor);
  font-size: 16px;
  font-weight: 500;
  white-space: nowrap;
}
.sidebar.close .profile-details i,
.sidebar.close .profile-details .profile_name,
.sidebar.close .profile-details .job {
  display: none;
}
.sidebar .profile-details .job {
  font-size: 12px;
}
.home-section {
  position: relative;
  left: 260px;
  width: calc(100% - 260px);
  transition: all 0.5s ease;
}
.sidebar.close ~ .home-section {
  left: 78px;
  width: calc(100% - 78px);
}
.home-section .home-content {
  display: flex;
  align-items: center;
  font-family: "Montserrat", sans-serif;
}
.home-section .home-content .bx-menu,
.home-section .home-content .bx-x,
.home-section .home-content .text {
  color: var(--ListHover);
  font-size: 35px;
}
.home-section .home-content .bx-menu,
.bx-x {
  margin: 0 15px;
  cursor: pointer;
}
.home-section .home-content .text {
  font-size: 26px;
  font-weight: 600;
}

.bx-left-arrow-alt {
  color: var(--ListHover);
  font-size: 35px;
  margin: 0 15px;
  cursor: pointer;
  text-align: end;
}
.row {
  display: flex;
  flex-wrap: wrap;
}
ul {
  list-style: none;
}
.sticky {
  position: fixed;
  top: 0;
}
.notification{
  position: fixed;
  bottom: 40px;
  left: 90px;
  width: max-content;
  padding: 20px 15px;
  border-radius: 4px;
  background-color: #141619;
  color: #f6f5f9;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.1);
  transform: translateY(30px);
  opacity: 0;
  visibility:hidden;
  animation: fade-in 4s linear forwards;
  z-index: 1000;
}
.notification-progress{
  position: absolute;
  left: 5px;
  bottom: 5px;
  width: calc(100% - 10px);
  transform: scaleX(0);
  height: 3px;
  transform-origin: left;
  background-image: linear-gradient( to right , #539bdb, #3250bf);
  border-radius: inherit;
  animation:load 3s 0.25s linear forwards;
}
.search{
  position: relative;
  width: 60px;
  width: 60px;
  border-radius: 60px;
  transition: 0.5s;
  box-shadow: 0 0 0 3px var(--ListHover);
  overflow: hidden;
}
.search .icon{
  position: absolute;
  top: 0;
  left: 0;
  width: 60px;
  height: 60px;
  background: #fff;
  border-radius: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.search .icon::before{
  content: '';
  position: absolute;
  width: 15px;
  height: 15px;
  border: 3px solid #287dfc;
  border-radius: 50%;
  transform: translate(-4px,-4px) ;
}.search .icon::after{
  content: '';
  position: absolute;
  width: 3px;
  height: 12px;
  background:  #287dfc;
  transform: translate(6px,6px) rotate(315deg);
}
.search.active{
width: 360px;
}
.search .input{
position: relative;
width: 290px;
height: 60px;
left: 60px;
display: flex;
justify-content: center;
align-items: center;
}
.search .input input{
  position: absolute;
  top: 0;
  height: 100%;
  width: 100%;
  outline: none;
  border: none;
  font-size: 18px;
  padding: 0px 0px;
}

/*Content Container*/
.container {
  height: 100%;
  width: 100%;
  display: flex;
  font-family: "Montserrat", sans-serif;
  flex-wrap: wrap;
  padding: 8px 15px;
  margin-left: 78px;
  max-width: 1200px;
}
.Product {
  padding: 10px;
}
.card {
  position: relative;
  width: 130px;
  height: 165px;
  background: var(--BgSecoundColor);
  border-radius: 20px;
  overflow: hidden;
}

.card::before {
  content: "";
  position: absolute;
  top: -50%;
  width: 100%;
  height: 100%;
  background: var(--CardColor);
  transform: skewY(345deg);
  transition: 0.5s;
 

}
.card:hover::before {
  top: -50%;
  transform: skewY(390deg);
  box-shadow:   inset 0 0 50px var(--BgSecoundColor),      /* inner white */
  inset 20px 0 80px #fff,   /* inner left magenta short */
  inset -20px 0 80px #0ff,  /* inner right cyan short */
  inset 20px 0 300px #f0f,  /* inner left magenta broad */
  inset -20px 0 300px #0ff, /* inner right cyan broad */
  0 0 50px  var(--BgSecoundColor),            /* outer white */
  -10px 0 80px #f0f,        /* outer left magenta */
  10px 0 80px #0ff;
}
.card .imgBox {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 20px;
  z-index: 1;
}

.card .imgBox img {
  max-width: 100%;
  border-radius: 10px;
  transition: 0.5s;
}
.card .contentBox {
  position: relative;
  padding: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  z-index: 2;
}

.card .contentBox h3 {
  font-size: 12px;
  color: var(--TextColor);
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.card .contentBox .price {
  font-size: 18px;
  color: var(--TextColor);
  font-weight: 700;
  letter-spacing: 1px;
  margin-top: -4px;
}

.card .contentBox .buy {
  position: relative;
  top: 110px;
  opacity: 0;
  padding: 8px 10px;
  margin-top: -44px;
  color: #000000;
  text-decoration: none;
  background: var(--ButtonColor);
  border-radius: 30px;
  text-transform: uppercase;
  letter-spacing: 0px;
  transition: 0.5s;
}
.card .contentBox a {
  font-size: 12px;
}
.card:hover .contentBox .buy {
  top: 0;
  opacity: 1;
}

.mouse {
  height: 64px;
  width: auto;
}

.hero {
  width: 100%;
  height: 100%;
  padding: 10px 1%;
  position: relative;
}
.hero h3 {
  font-weight: 400;
  font-size: 30px;
}
.hero h1 {
  margin-top: 30px;
  font-size: 32px;
  color:var(--ListHover) ;
}
.hero h1 #title{
  color: #287dfc;
 
}
.hero h3 #home::before {
  content: "";
  animation: animate ease-in-out infinite 30s;
  color: #287dfc;
}
.hero .left-hero{
  float: left;
}
.hero .right-hero{
  float: right;
}
.hero p {
  margin: 10px 0 25px;
}
.privacy {
  padding: 20px;
}
.privacy h2,
.privacy p,
.privacy ul {
  padding: 1px;
}
.privacy li {
  list-style: none;
  padding: 3px;
}
.privacy h1 {
  margin-bottom: 4px;
}

/*Footer*/
.footer {
  position: relative;
  font-family: "Montserrat", sans-serif;
  bottom: -115px;
  width: 100%;
  background: var(--ListHover);
  padding-top: 60px;
}
footer a,
footer span {
  cursor: pointer;
  text-decoration: none;
}
footer span {
  color: var(--TextColor);
}
footer {
  display: flex;
  align-items: flex-start;
  gap: 40px;
  margin: 0 30px;
}
footer a {
  color: var(--LinkColor);
}
footer a:hover,
footer span:hover {
  color: var(--LinkHover);
}

footer.bottom {
  padding: 20px 0;
  justify-content: space-between;
}

.legal {
  font-size: 16px;
}

.legal > span {
  margin: 0 4px;
  padding: 5px;
}

.legal > a {
  margin-right: 10px;
}

footer.bottom .links {
  display: flex;
  gap: 18px;
}

footer.bottom .links > a {
  font-size: 24px;
}
.footer .author {
  display: block;
  width: 100%;
  height: 100%;
}
.footer .author p {
  text-align: center;
  color: var(--TextColor);
}



.accordion {
  margin:0 auto;
  width: 90%;
  cursor: text;
}
.accordion li {
  list-style: none;
  width: 100%;
  margin: 20px;
  padding: 10px;
  border-radius: 8px;
  background: #e3edf7;
  box-shadow: 6px 6px 10px -1px rgba(0, 0, 0, 0.15),
    -6px -6px 10px -1px rgba(255, 255, 255, 0.7);
}
.accordion li label {
  display: flex;
  align-items: center;
  padding: 10px;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
}
.accordion li label::before {
  font-family: FontAwesome;
  content: "\f0d7";
  margin-right: 10px;
  font-size: 24px;
  color: #7257fa;
  font-weight: 600;
}
.accordion input[type="radio"] {
  display: none;
}
.accordion .content {
  padding: 0 10px;
  line-height: 26px;
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.5s, padding 0.5s;
}
.accordion input[type="radio"]:checked + label + .content {
  max-height: 600px;
  padding: 10px 10px 20px;
  overflow: scroll;
}
.accordion input[type="radio"]:checked + label + .content::-webkit-scrollbar {
  display: none;
}
.accordion input[type="radio"]:checked + label::before {
  font-family: FontAwesome;
  content: "\f0d8";
}
.toolCard {
 
  width: 260px;
  padding: 1rem;
  margin-bottom: 20px;
  margin-right: 8px;
  cursor: pointer;
  display: inline-block;
  border-radius: var(--border-radius);
  background: #f1f1f3;
  box-shadow: 0px 8px 16px 0px rgb(0 0 0 / 3%);
  position: relative;
  justify-content: center;
  z-index: 1;
  height: 164px;
  max-height: 200px;
  display: inline-grid;
}

.toolCard > * + * {
  margin-top: 1.1em;
}
.toolCard .card__content {
  color: var(--secondary-color);
  font-size: 0.86rem;
  margin-top: 5px;
}
.toolCard .card__title {
  padding: 0;
  font-size: 1.3rem;
  font-weight: bold;
  color: #111;
}
.toolCard .card__arrow {
  position: absolute;
  background: var(--primary-color);
  padding: 0.4rem;
  border-top-left-radius: var(--border-radius);
  border-bottom-right-radius: var(--border-radius);
  bottom: 0;
  right: 0;
  transition: 0.2s;
  display: flex;
  justify-content: center;
  align-items: center;
}
.toolCard .card__like {
  position: absolute;
  background: var(--primary-color);
  padding: 0.4rem;
  border-top-right-radius: var(--border-radius);
  border-bottom-left-radius: var(--border-radius);
  top: 0;
  right: 0;
  transition: 0.2s;
  display: flex;
  justify-content: center;
  align-items: center;
}
.toolCard .card__date {
  color: #6e6b80;
  font-size: 0.8rem;
  height: 20px;
  margin: 0;
  width: 200px;
  overflow-x: auto;
  overflow-y: auto;
}
.toolCard .card__date::-webkit-scrollbar {
  display: none;
}
.toolCard a .card__arrow,
.card__like {
  color: #111;
}
.toolCard i {
  transition: 0.2s;
}
.toolCard:hover {
  border: 1px solid white;
}
.toolCard:hover .card__title {
  color: var(--primary-color);
  text-decoration: underline;
}
.toolCard .card__arrow:hover {
  background: #111;
}
.toolCard .card__like:hover {
  background: #111;
}
.toolCard .card__arrow:hover i {
  transform: translateX(3px);
  color: #7257fa;
}
.toolCard .card__like:hover i {
  font-size: 17px;
  color: #7257fa;
}
.toolCardTitle h3 {
  margin: 20px;
  text-align: center;
  color: #666;
}
#notfound {
  user-select: none;
  height: 100%;
  display: none;
  z-index: 0;
  text-align: center;
}
#notfound .notfound {
  position: static;
  left: 50%;
  top: 40%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(80%, 8%);
}
.notfound {
  width: 100%;
  line-height: 1.4;
  text-align: center;
}
.notfound .notfound-404 {
  position: relative;
  height: 200px;
  margin: 0px auto 20px;
  z-index: -1;
}
.notfound .notfound-404 h1 {
  font-size: 236px;
  font-weight: 200;
  margin: 0px;
  color: #211b19;
  text-transform: uppercase;
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.notfound .notfound-404 h2 {
  font-size: 28px;
  font-weight: 400;
  text-transform: uppercase;
  color: #211b19;
  background: #fff;
  padding: 10px 5px;
  margin: auto;
  display: inline-block;
  position: absolute;
  bottom: 0px;
  left: 0;
  right: 0;
}
.notfound a {
  display: inline-block;
  font-weight: 700;
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  padding: 13px 23px;
  background: #4a98f7;
  font-size: 18px;
  -webkit-transition: 0.2s all;
  transition: 0.2s all;
}
.notfound a:hover {
  color: #f0faff;
  background: #211b19;
}

.Login {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 260px;
  padding: 20px 1px;
  margin: 20px auto;
  text-align: center;
  position: relative;
  box-shadow: 0 10px 15px -3px rgba(33, 150, 243, 0.4),
    0 4px 6px -4px rgba(33, 150, 243, 0.4);
  border-radius: 10px;
  background-color: #6b6ecc;
  background: linear-gradient(45deg, #04051dea 0%, #2b566e 100%);
}

.Login .content {
  padding: 20px;
}

.Login .content .price {
  color: white;
  font-weight: 800;
  font-size: 50px;
  text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.42);
}

.Login .content .description {
  color: rgba(255, 255, 255, 0.6);
  margin-top: 10px;
  font-size: 14px;
