.Login button:hover{
  background-color: rgb(30, 111, 176);
}
#freePlan{
  background-color: rgb(30, 111, 176);
}
.about-section {
  font-family: "Montserrat", sans-serif;
}
.inner-container h1 {
  margin-bottom: 30px;
  font-size: 30px;
  font-weight: 900;
}
.about-section .text {
  font-variant: unset;
  line-height: 30px;
  margin-bottom: 40px;
  font-size: medium;
  color: #555;
  font-weight: 200;
}

.contact {
  width: 100%;
  height: 100%;
  display: flex;
  background: #f1f1f1;
  align-items: center;
  justify-content: center;
  padding: 12px 0 12px 0;
}
.ContactForm {
  background: #fff;
  display: flex;
  flex-direction: column;
  padding: 2vw 4vw;
  width: 90%;
  max-width: 600px;
  border-radius: 10px;
}
.ContactForm h3 {
  color: #555;
  font-weight: 800;
  margin-bottom: 20px;
}
.ContactForm p {
  font-size: medium;
  color: #555;
  font-weight: 200;
}
.ContactForm input,
.ContactForm textarea {
  border: 0;
  margin: 10px 0;
  padding: 20px;
  outline: none;
  background: #f5f5f5;
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
}
.ContactForm button {
  padding: 15px;
  background: #f24d41;
  color: #fff;
  font-size: 18px;
  border: 0;
